import { headerSearch } from "../../plugins/ComponentsUi/HeaderSearch/HeaderSearch";
import { detectCloseElement } from "./helper";
/*==================== Header ====================*/
/**
 * @param header
 */
const vw = $(window).width();
export const header = {
	scrollActive: function () {
		let height = $("header").height();
		if ($(window).scrollTop() > height) {
			$("header").addClass("active");
		} else {
			$("header").removeClass("active");
		}
	},
	mobile: function () {
		$(".header-bar").on("click", function () {
			$(this).toggleClass("active");
			$("body").toggleClass("isOpenMenu");

			// i icon
			const $icon = $(this).find("i");
			$icon.toggleClass("fa-bars fa-xmark");
		});

		$(
			".header-nav-mobile .sub-menu, .header-nav-mobile .sub-menu-children, .header-nav-mobile .sub-menu-children-dropdown"
		).hide();

		$('.header-nav-mobile li[class*="menu-item-has-children"] > a').on("click", function (e) {
			e.preventDefault();

			$(this)
				.toggleClass("dropdown-active")
				.next()
				.slideToggle()
				.parent()
				.siblings()
				.find("a")
				.removeClass("dropdown-active")
				.next()
				.slideUp();
		});
	},
	initVariable: function () {
		const $header = document.querySelector("header");
		if (!$header) return;

		// Hm cp nht chiu cao header
		function updateHeaderHeight() {
			const height = $header.offsetHeight;
			document.documentElement.style.setProperty("--header-height", `${height}px`);
		}

		// Cp nht ban u
		updateHeaderHeight();

		// Theo di mi thay i chiu cao ca header
		const ro = new ResizeObserver(updateHeaderHeight);
		ro.observe($header);

		// Phng trng hp nh hoc font cha load xong
		window.addEventListener("load", () => {
			setTimeout(updateHeaderHeight, 100);
		});
	},
	init: function () {
		headerSearch();
		header.scrollActive();
		header.mobile();
		header.initVariable();
	},
};
document.addEventListener(
	"scroll",
	function (e) {
		header.scrollActive();
	},
	true
);
